.ImageFirstAppearance {
    display: flex;
    background-image: url(../../../img/img/expozitii.png);
    background-size: cover;
    position: relative;
    height: 100vh;
    background-repeat: no-repeat;
    margin-bottom: 7.56rem;
  }
  @media screen and (max-width: 1253px) {
    .ImageFirstAppearance {
      width: 100%;
    }
  }
  .accordion-body {
    color: white;
  }
  
  .textImageFirstAppearance {
    width: 50%;
    color: rgb(255, 250, 250);
    font-family: "Bai Jamjuree", sans-serif;
    font-size: 2.5rem;
    font-weight: 600;
    line-height: 115.625%;
    margin-right: auto;
  }
  .textYellowImageFirstAppearance {
    color: #fcd900;
  }
  
  .textSmallImageFirstAppearance {
    width: 50%;
    font-size: 1.4rem;
    color: white;
  }
  .textContainer {
    outline: none;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
  }
  
  .customBtnFirstAppearance {
    padding: 10px 20px;
    margin-bottom: 15px;
  }
  
  .customBtnFirstAppearance:focus {
    color: white;
  }
  @media screen and (max-width: 992px) {
    .textImageFirstAppearance {
      font-size: 2rem;
      width: 70%;
    }
    .textSmallImageFirstAppearance {
      width: 70%;
    }
  }
  @media screen and (max-width: 768px) {
    .textImageFirstAppearance {
      font-size: 2rem;
      width: 100%;
    }
    .textSmallImageFirstAppearance {
      width: 100%;
    }
  }
  @media screen and (max-width: 437px) {
    .textImageFirstAppearance {
      font-size: 1.7rem;
      width: 100%;
    }
    .textSmallImageFirstAppearance {
      color: #d0d0d0;
      width: 100%;
    }
  }
  
  /* TEXT DUPA IMAGINE */
  
  .sectionOccupation {
    display: flex;
    justify-content: space-around;
    margin-bottom: 6rem;
  }
  
  
  .sectionOccupationText {
    outline: none;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    width: 70rem;
  }
  
  .sectionOccupationTextBig {
    width: 90%;
    font-family: Roboto;
    font-size: 2rem;
    font-style: normal;
    font-weight: 400;
    margin-bottom: 1.5rem;
  }
  
  .sectionOccupationTextSmall {
    width: 80%;
    font-family: Roboto;
    margin-bottom: 3.5rem;
  }
  
  .btn-sectionOccupationButton {
    color: black;
    background-color: bisque;
    margin-bottom: 7.44rem;
  }
  .leftSectionOccupation {
    display: flex;
    flex: 1;
    margin-left: 6%;
    width: 35%;
  }
  .rightSectionOccupation {
    flex: 1;
    max-width: 55%;
  }
  .rightSectionOccupationList {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin-bottom: 0;
  }
  
  
  .imgDoiOameni {
    width: 100%;
    height: 90%;
    border-radius: 5%;
  }
  .flagMoldova{
    width: 100%;
    height: 90%;
    border-radius: 5%;
  }
  @media screen and (max-width: 1200px) {
    .flagMoldova{
      height: 80%;
      display: flex;
      margin-right: auto;
      margin-left: auto;
    }
    .leftSectionOccupation {
      display: flex;
      margin-left: 0;
      width: 40%;
    }
    .rightSectionOccupationList {
      display: flex;
      flex-direction: row;
      list-style: none;
      margin-bottom: 0;
      justify-content: space-between;
    }
    .rightSectionOccupation {
      max-width: 100%;
      margin-bottom: 5rem;
    }
  }
  @media screen and (max-width: 990px){
    .leftSectionOccupation{
      width: 50%;
    }
    .sectionOccupationTextSmall{
      font-size: 0.8rem;
    }
    .flagMoldova{
      width: 100%;
    }
    }
    @media screen and (max-width: 768px) {
      .leftSectionOccupation{
        width: 100%;
      }
      .flagMoldova{
        width: 60%;
      }
      .sectionOccupation{
        display: block;
      }
      .rightSectionOccupationList {
        display: block;
        flex-direction: column;
        list-style: none;
        justify-content: space-between;
      }
      .rightSectionOccupation {
        flex: 1;
        max-width: 100%;
      }
      .btn-sectionOccupationButton {
        color: black;
        background-color: bisque;
        margin-bottom: 1rem;
      }
    }
  /* Citat dupa sectie */
  .sectionQuote{
    margin-bottom: 6rem;
  }
  .sectionQuoteText {
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    font-family: Roboto;
    font-size: 2rem;
    line-height: 2.75rem;
  }
  .sectionQuoteParagraph {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    font-family: Roboto;
    font-size: 1.125rem;
  }
  
  
  /* SECTION SERVICII */
  .sectionServiciiCards {
    justify-content: center;
  }
  
  .sectionServiciiCards {
    display: flex;
    flex-direction: row;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .sectionServiciiCardSecondParagraph {
    margin-bottom: 150px;
  }
  
  .sectionServiciiCardImage {
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    margin-bottom: 1.1rem;
    border-radius: 15%;
  }
  
  .sectionServiciiCard {
    width: 40%;
    margin-bottom: 7rem;
  }
  
  .sectionServiciiCardParagraph {
    width: 80%;
    font-family: Roboto;
    font-size: 1rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5rem;
    letter-spacing: 0.0225rem;
  }
  
  @media screen and (max-width: 998px) {
    li:nth-child(n) {
      display: block !important;
    }
    .sectionServiciiCards {
      display: block;
      list-style: none;
      margin: 0;
      padding: 0;
      width: 100%;
    }
    .sectionServiciiCard {
      width: 100%;
      margin-bottom: 7rem;
    }
    .sectionServiciiCardCiteste {
      font-weight: 400;
    }
  
    .sectionServiciiCardParagraph {
      width: 70%;
    }
    .sectionServiciiCardImage {
      width: 70%;
      height: 300px;
      display: flex;
      margin-right: auto;
      margin-left: auto;
      margin-bottom: 1.1rem;
      border-radius: 15%;
    }
  }
  @media screen and (max-width: 500px) {
    .sectionServiciiCardImage {
      width: 100%;
    }
  }
  /* SECTION sectionQuoteAfterServicii*/
  .sectionQuoteAfterServicii {
    display: flex;
    justify-content: center;
    margin-bottom: 8rem;
  }
  .sectionQuoteAfterServiciiParagraph {
    width: 50%;
    font-family: Roboto;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 400;
  }
  @media screen and (max-width: 770px) {
    .sectionQuoteAfterServiciiParagraph {
      width: 90%;
    }
  }
  
  /* Section Video */
  
  .sectionVideo {
    display: flex;
    justify-content: center;
  }
  .sectionVideoParagraph {
    width: 50%;
    font-family: Roboto;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 400;
  }
  .sectionVideo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 6rem;
  }
  